@charset "UTF-8";
* {
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

body {
  background-color: #0a192f;
}

.custom-btn {
  color: #64ffda;
  border: 1px solid #64ffda;
  text-transform: capitalize;
  font-family: "Fira Code", monospace;
  font-size: 15px;
  padding: 15px 30px;
}
.custom-btn:hover {
  color: #64ffda;
  background-color: rgba(100, 255, 218, 0.2);
}

.heading {
  color: #e6f1ff;
  display: flex;
  flex-direction: row;
  position: relative;
  align-items: center;
  margin: 10px 0px 40px;
  font-family: "Roboto", sans-serif;
  font-size: 28px;
  font-weight: 600;
}
.heading::before {
  content: "0" attr(data-before-content) ".";
  text-align: right;
  color: #64ffda;
  font-size: 23px;
  font-family: "Fira Code", monospace;
}
.heading::after {
  content: "";
  display: block;
  height: 1px;
  width: 300px;
  background-color: #303c55;
  position: relative;
  margin-left: 20px;
}

.container {
  padding: 0 100px;
}

.navbar-style {
  transition: all 0.25s cubic-bezier(0.645, 0.045, 0.355, 1) 0s;
  z-index: 100;
  color: #e6f1ff;
  padding: 1 px 27px;
  position: fixed;
  background-color: #0a192f;
  width: 100%;
}
.navbar-style .navbar a {
  color: #ccd6f6;
  transition: all ease-in-out 0.3s;
  -webkit-transition: all ease-in-out 0.3s;
  -moz-transition: all ease-in-out 0.3s;
  -ms-transition: all ease-in-out 0.3s;
  -o-transition: all ease-in-out 0.3s;
}
.navbar-style .navbar a:hover {
  color: #64ffda;
}

.navbar-style nav ul li {
  position: relative;
  padding: 0 27px;
  font-size: 13px;
  font-family: "Fira Code", monospace;
}
.navbar-style nav ul li::before {
  content: "0" attr(data-before-content) ".";
  text-align: right;
  color: #64ffda;
  font-size: 12px;
}
.navbar-style nav ul li a {
  display: inline-block;
  -webkit-text-decoration-skip: ink;
          text-decoration-skip-ink: auto;
}

.navbar-style nav ul li.resume-btn {
  border: 1px solid #64ffda;
  border-radius: 2px;
  -webkit-border-radius: 2px;
  -moz-border-radius: 2px;
  -ms-border-radius: 2px;
  -o-border-radius: 2px;
}
.navbar-style nav ul li.resume-btn::before {
  content: "";
  text-align: right;
  font-size: 12px;
}
.navbar-style nav ul li.resume-btn a {
  color: #64ffda;
}

.up {
  transform: translateY(-80px);
  -webkit-transform: translateY(-80px);
  -moz-transform: translateY(-80px);
  -ms-transform: translateY(-80px);
  -o-transform: translateY(-80px);
}

.shadow {
  box-shadow: rgba(2, 12, 27, 0.7) 0px 10px 30px -10px;
}

.nav-mobile-larg {
  transition: all 0.25s cubic-bezier(0.645, 0.045, 0.355, 1) 0s;
  z-index: 100;
  color: #e6f1ff;
  padding: 1 px 27px;
  position: fixed;
  background-color: #0a192f;
  width: 100%;
  display: none;
}
.nav-mobile-larg .toggler-bar {
  color: #64ffda;
}
.nav-mobile-larg .toggler-bar ul {
  list-style: none;
  direction: rtl;
  padding-right: 0;
  cursor: pointer;
}
.nav-mobile-larg .toggler-bar ul li {
  margin: 10px 0;
  transition: all ease-in-out 0.3s;
  -webkit-transition: all ease-in-out 0.3s;
  -moz-transition: all ease-in-out 0.3s;
  -ms-transition: all ease-in-out 0.3s;
  -o-transition: all ease-in-out 0.3s;
}
.nav-mobile-larg .toggler-bar ul span {
  width: 50px;
  height: 2px;
  background-color: #64ffda;
  display: block;
  transition: all ease-in-out 0.3s;
  -webkit-transition: all ease-in-out 0.3s;
  -moz-transition: all ease-in-out 0.3s;
  -ms-transition: all ease-in-out 0.3s;
  -o-transition: all ease-in-out 0.3s;
}
.nav-mobile-larg .toggler-bar ul span.s {
  width: 40px;
}
.nav-mobile-larg .toggler-bar ul span.t {
  width: 30px;
  transition: all ease-in-out 0.3s;
  -webkit-transition: all ease-in-out 0.3s;
  -moz-transition: all ease-in-out 0.3s;
  -ms-transition: all ease-in-out 0.3s;
  -o-transition: all ease-in-out 0.3s;
}
.nav-mobile-larg .toggler-bar.active {
  color: #64ffda;
}
.nav-mobile-larg .toggler-bar.active ul {
  list-style: none;
  direction: rtl;
  padding-right: 0;
  cursor: pointer;
}
.nav-mobile-larg .toggler-bar.active ul li {
  margin: -2px 0;
}
.nav-mobile-larg .toggler-bar.active ul li span {
  width: 40px;
  height: 2px;
  background-color: #64ffda;
  display: block;
}
.nav-mobile-larg .toggler-bar.active ul li span.s {
  width: 40px;
}
.nav-mobile-larg .toggler-bar.active ul li span.t {
  width: 40px;
}

.nav-mobile {
  display: none;
  position: fixed;
  right: -300px;
  top: 0;
  background-color: #172a45;
  width: 300px;
  height: 100vh;
  text-align: center;
  padding: 30px;
  transition: all ease-in-out 0.3s;
  -webkit-transition: all ease-in-out 0.3s;
  -moz-transition: all ease-in-out 0.3s;
  -ms-transition: all ease-in-out 0.3s;
  -o-transition: all ease-in-out 0.3s;
}
.nav-mobile .logo {
  margin-bottom: 50px;
}
.nav-mobile .item-mobile {
  margin: 20px 0;
}
.nav-mobile .item-mobile::before {
  content: "0" attr(data-before-content) ".";
  color: #64ffda;
  font-size: 15px;
  display: block;
  margin: auto;
}
.nav-mobile .item-mobile a {
  color: #ccd6f6;
  font-family: "Fira Code", monospace;
  text-decoration: none;
  font-size: 17px;
}
.nav-mobile .x {
  display: flex;
  justify-content: flex-end;
}
.nav-mobile .x ion-icon {
  color: #64ffda;
  font-size: 60px;
  text-align: left;
  cursor: pointer;
  transform: rotate(30deg);
  -webkit-transform: rotate(30deg);
  -moz-transform: rotate(30deg);
  -ms-transform: rotate(30deg);
  -o-transform: rotate(30deg);
  transition: all ease-in-out 0.05s;
  -webkit-transition: all ease-in-out 0.05s;
  -moz-transition: all ease-in-out 0.05s;
  -ms-transition: all ease-in-out 0.05s;
  -o-transition: all ease-in-out 0.05s;
  transition-delay: 0.3s;
}

.nav-mobile.active {
  right: 0px;
}
.nav-mobile.active ion-icon {
  transform: rotate(90deg);
  -webkit-transform: rotate(90deg);
  -moz-transform: rotate(90deg);
  -ms-transform: rotate(90deg);
  -o-transform: rotate(90deg);
}

.hero {
  padding: 150px 0;
}
.hero span {
  color: #64ffda;
  font-family: "Fira Code", monospace;
}
.hero h1 {
  font-size: 70px;
  font-family: "Roboto", sans-serif;
  font-weight: 900;
  margin-top: 15px;
}
.hero .light {
  color: #ccd6f6;
}
.hero .dark {
  color: #8892b0;
}
.hero p {
  color: #8892b0;
  max-width: 500px;
  font-size: 17px;
  margin-top: 50px;
}
.hero .custom-btn {
  margin-top: 30px;
  border-radius: 1px;
  -webkit-border-radius: 1px;
  -moz-border-radius: 1px;
  -ms-border-radius: 1px;
  -o-border-radius: 1px;
}

.overlay {
  position: relative;
}
.overlay .left {
  width: 40px;
  position: fixed;
  bottom: 0px;
  left: 30px;
}
.overlay ul {
  display: flex;
  list-style: none;
  flex-direction: column;
  align-items: center;
  margin-bottom: 0px;
}
.overlay ul::after {
  content: "";
  display: block;
  width: 1px;
  height: 90px;
  background-color: #a8b2d1;
  margin: 0px auto;
}
.overlay ul li {
  padding-bottom: 18px;
  font-size: 23px;
}
.overlay ul a {
  color: #8892b0;
  position: relative;
  bottom: 0px;
  transition: all ease-in-out 0.2s;
  -webkit-transition: all ease-in-out 0.2s;
  -moz-transition: all ease-in-out 0.2s;
  -ms-transition: all ease-in-out 0.2s;
  -o-transition: all ease-in-out 0.2s;
}
.overlay ul a:hover {
  color: #64ffda;
  bottom: 3px;
}
.overlay .right {
  width: 40px;
  position: fixed;
  bottom: 0px;
  right: 30px;
}
.overlay .right div {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.overlay .right div::after {
  content: "";
  display: block;
  width: 1px;
  height: 90px;
  background-color: #a8b2d1;
  margin: 0px auto;
}
.overlay .right a {
  writing-mode: vertical-lr;
  -webkit-writing-mode: vertical-lr;
  -ms-writing-mode: vertical-lr;
  padding: 18px;
  font-size: 13px;
  letter-spacing: 1px;
  text-decoration: none;
  color: #8892b0;
  font-family: "Fira Code", monospace;
  transition: all ease-in-out 0.2s;
  -webkit-transition: all ease-in-out 0.2s;
  -moz-transition: all ease-in-out 0.2s;
  -ms-transition: all ease-in-out 0.2s;
  -o-transition: all ease-in-out 0.2s;
}
.overlay .right a:hover {
  color: #64ffda;
}

.about {
  padding: 150px;
  font-family: "Roboto", sans-serif;
  font-size: 25px;
  font-weight: 600;
}
.about .text p {
  font-size: 16px;
  font-weight: 300;
  color: #8892b0;
}
.about .text p a {
  color: #64ffda;
  display: inline-block;
  text-decoration: none;
  transition: all ease-in-out 0.3s;
  -webkit-transition: all ease-in-out 0.3s;
  -moz-transition: all ease-in-out 0.3s;
  -ms-transition: all ease-in-out 0.3s;
  -o-transition: all ease-in-out 0.3s;
}
.about .text p a:hover {
  color: rgba(100, 255, 218, 0.2);
}
.about .text ul {
  list-style: none;
  display: grid;
  grid-template-columns: 1fr 1fr;
  padding-left: 0px;
}
.about .text ul li {
  font-weight: 300;
  font-size: 13px;
  font-family: "Fira Code", monospace;
  color: #8892b0;
}
.about .text ul li::before {
  content: "▹";
  color: #64ffda;
  padding-right: 10px;
}
.about .image-container {
  padding-left: 50px;
  position: relative;
}
.about .image-container .img-filter {
  width: 350px;
  height: 350px;
  position: relative;
}
.about .image-container .img-filter .bg-layer {
  position: absolute;
  z-index: 3;
  background-color: rgba(100, 255, 218, 0.5);
  width: 100%;
  height: 100%;
  transition: all ease-in-out 0.5s;
  -webkit-transition: all ease-in-out 0.5s;
  -moz-transition: all ease-in-out 0.5s;
  -ms-transition: all ease-in-out 0.5s;
  -o-transition: all ease-in-out 0.5s;
}
.about .image-container .img-filter img {
  width: 100%;
  position: relative;
  z-index: 1;
}
.about .image-container .back-border {
  width: 350px;
  height: 350px;
  border: 3px solid #64ffda;
  position: absolute;
  z-index: 0;
  top: 30px;
  left: 80px;
  transition: all ease-in-out 0.5s;
  -webkit-transition: all ease-in-out 0.5s;
  -moz-transition: all ease-in-out 0.5s;
  -ms-transition: all ease-in-out 0.5s;
  -o-transition: all ease-in-out 0.5s;
}
.about .image-container:hover .img-filter .bg-layer {
  background-color: transparent;
}
.about .image-container:hover .back-border {
  top: 20px;
  left: 70px;
}

.work-history {
  padding: 150px 0;
}
.work-history .rwt__tablist[aria-orientation=vertical] {
  border-right: 0px;
  border-left: unset;
  margin-right: 2rem;
}
.work-history .rwt__tab[role=tab] {
  color: #8892b0;
  font-family: "Fira Code", monospace;
  font-size: 13px;
  background-color: transparent;
  padding: 11px 40px 11px 20px;
  border-left: 1px solid #8892b0;
  text-align: left;
}
.work-history .rwt__tab[role=tab]:hover {
  background: unset;
  background-color: rgba(100, 255, 218, 0.2);
}
.work-history .rwt__tab[role=tab][aria-selected=true] {
  background-color: rgba(100, 255, 218, 0.2);
  color: #64ffda;
}
.work-history .rwt__tablist[aria-orientation=vertical] .rwt__tab[aria-selected=true]:after {
  right: unset;
  left: -2px;
  border-right: 2px solid #64ffda;
}
.work-history .content {
  max-width: 700px;
  margin: auto;
  color: #e6f1ff !important;
}
.work-history .content .job-heading {
  color: #a8b2d1;
  font-family: "Roboto", sans-serif;
}
.work-history .content .job-heading span a {
  color: #64ffda;
  text-decoration: none;
}
.work-history .content .job-heading p {
  font-size: 13px;
  color: #8892b0;
  font-weight: 600;
  font-family: "Fira Code", monospace;
}
.work-history .content .job-des {
  color: #8892b0;
}
.work-history .content .job-des ul {
  padding-left: 0px;
  list-style: none;
}
.work-history .content .job-des ul li {
  margin-bottom: 10px;
  position: relative;
  padding-left: 30px;
  font-size: 15px;
  font-family: "Roboto", sans-serif;
  font-weight: 500;
}
.work-history .content .job-des ul li::before {
  content: "▹";
  color: #64ffda;
  position: absolute;
  left: 0px;
}

.portfolio {
  padding: 150px 0;
  position: relative;
}
.portfolio .content .align:nth-child(even) .image {
  grid-column: 6/-1;
  grid-row: 1/-1;
}
.portfolio .content .align:nth-child(even) .text {
  grid-column: 1/7;
  grid-row: 1/-1;
  text-align: left;
}
.portfolio .content .align:nth-child(even) ul {
  justify-content: end !important;
  padding-left: unset;
}
.portfolio .content .align:nth-child(even) ul li {
  padding-left: 0;
  padding-right: 15px;
}
.portfolio .content .cont-container {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  align-items: center;
  margin-bottom: 100px;
  gap: 10px;
}
.portfolio .content .cont-container .image {
  grid-column: 1/8;
  grid-row: 1/-1;
}
.portfolio .content .cont-container .image img {
  width: 100%;
}
.portfolio .content .cont-container .image .img-filter {
  width: 100%;
  height: 100%;
  position: relative;
}
.portfolio .content .cont-container .image .img-filter .bg-layer {
  position: absolute;
  z-index: 0;
  background-color: rgba(100, 255, 218, 0.5);
  width: 100%;
  height: 100%;
  transition: all ease-in-out 0.5s;
  -webkit-transition: all ease-in-out 0.5s;
  -moz-transition: all ease-in-out 0.5s;
  -ms-transition: all ease-in-out 0.5s;
  -o-transition: all ease-in-out 0.5s;
}
.portfolio .content .cont-container .image a:hover .bg-layer {
  background-color: transparent;
}
.portfolio .content .cont-container .text {
  text-align: right;
  grid-column: 7/-1;
  grid-row: 1/-1;
  position: relative;
}
.portfolio .content .cont-container .text span {
  color: #64ffda;
  font-family: "Fira Code", monospace;
  font-size: 14px;
}
.portfolio .content .cont-container .text h4 {
  color: #e6f1ff;
  margin: 22px 0;
  font-weight: 700;
}
.portfolio .content .cont-container .text h4 a {
  color: #e6f1ff;
  text-decoration: none;
}
.portfolio .content .cont-container .text h4 a:hover {
  color: #64ffda;
  transition: all ease-in-out 0.5s;
  -webkit-transition: all ease-in-out 0.5s;
  -moz-transition: all ease-in-out 0.5s;
  -ms-transition: all ease-in-out 0.5s;
  -o-transition: all ease-in-out 0.5s;
}
.portfolio .content .cont-container .text p {
  color: #a8b2d1;
  background-color: #172a45;
  padding: 10px 20px;
}
.portfolio .content .cont-container .text ul {
  display: flex;
  flex-wrap: wrap;
  list-style: none;
  justify-content: flex-end;
}
.portfolio .content .cont-container .text li {
  padding-left: 15px;
  font-family: "Fira Code", monospace;
  color: #8892b0;
  font-size: 14px;
}
.portfolio .content .cont-container .text .links a {
  color: #ccd6f6;
  padding-left: 15px;
  font-size: 25px;
}
.portfolio .content .cont-container .text .links a:hover {
  color: #64ffda;
}

.projects {
  padding: 150px 0;
}
.projects h2 {
  text-align: center;
  color: #e6f1ff;
  font-family: "Roboto", sans-serif;
  font-weight: 600;
}
.projects .archive {
  color: #64ffda;
  font-family: "Fira Code", monospace;
  font-size: 13px;
  text-align: center;
  display: block;
  text-decoration: none;
  transition: all ease-in-out 0.3s;
  margin-bottom: 80px;
  -webkit-transition: all ease-in-out 0.3s;
  -moz-transition: all ease-in-out 0.3s;
  -ms-transition: all ease-in-out 0.3s;
  -o-transition: all ease-in-out 0.3s;
}
.projects .archive:hover {
  color: rgba(100, 255, 218, 0.2);
}
.projects .project-card {
  margin-top: 16px;
  position: relative;
  background-color: #172a45;
  padding: 30px 25px;
  border-radius: 3px;
  -webkit-border-radius: 3px;
  -moz-border-radius: 3px;
  -ms-border-radius: 3px;
  -o-border-radius: 3px;
  transition: all ease-in-out 0.2s;
  -webkit-transition: all ease-in-out 0.2s;
  -moz-transition: all ease-in-out 0.2s;
  -ms-transition: all ease-in-out 0.2s;
  -o-transition: all ease-in-out 0.2s;
