}
.projects .header .links a {
  color: #e6f1ff;
  font-size: 20px;
  margin: 5px;
  transition: all ease-in-out 0.2s;
  -webkit-transition: all ease-in-out 0.2s;
  -moz-transition: all ease-in-out 0.2s;
  -ms-transition: all ease-in-out 0.2s;
  -o-transition: all ease-in-out 0.2s;
}
.projects .header .links a:hover {
  color: rgba(100, 255, 218, 0.2);
}
.projects .text {
  margin-top: 20px;
}
.projects .text h4 {
  color: #e6f1ff;
  font-weight: 600;
  font-family: "Roboto", sans-serif;
  margin: 13px 0;
}
.projects .text h4 a {
  color: #e6f1ff;
  text-decoration: none;
}
.projects .text h4 a:hover {
  color: #64ffda;
  transition: all ease-in-out 0.5s;
  -webkit-transition: all ease-in-out 0.5s;
  -moz-transition: all ease-in-out 0.5s;
  -ms-transition: all ease-in-out 0.5s;
  -o-transition: all ease-in-out 0.5s;
}
.projects .text p {
  color: #a8b2d1;
  font-family: "Roboto", sans-serif;
  min-height: 150px;
}
.projects .text ul {
  display: flex;
  justify-content: end;
  list-style: none;
  color: #8892b0;
  padding-left: 0px;
  font-size: 13px;
  font-family: "Fira Code", monospace;
  font-weight: 600;
}
.projects .text ul li {
  margin-right: 13px;
}
.projects .col-md-4 {
  padding-left: 8px;
  padding-right: 8px;
}
.projects .more {
  margin-top: 80px;
  text-align: center;
}
.projects .more a {
  font-size: 13px;
}

.contact {
  padding: 150px 0;
  text-align: center;
}
.contact p.header {
  font-family: "Fira Code", monospace;
  color: #64ffda;
  text-transform: capitalize;
}
.contact p.header span {
  font-size: 15px;
}
.contact h1 {
  font-family: "Roboto", sans-serif;
  font-weight: 900;
  color: #e6f1ff;
  font-size: 50px;
  text-transform: capitalize;
  margin: 30px 0 40px 0;
}
.contact p.des {
  color: #a8b2d1;
  max-width: 550px;
  font-family: "Roboto", sans-serif;
  margin: auto;
  margin-bottom: 50px;
}
.contact a {
  font-size: 14px;
}

.footer {
  text-align: center;
  padding-right: 30px;
  margin-bottom: 20px;
}
.footer a {
  color: #8892b0;
  font-size: 13px;
  font-family: "Fira Code", monospace;
  text-decoration: none;
  transition: all ease-in-out 0.2s;
  -webkit-transition: all ease-in-out 0.2s;
  -moz-transition: all ease-in-out 0.2s;
  -ms-transition: all ease-in-out 0.2s;
  -o-transition: all ease-in-out 0.2s;
}
.footer a:hover {
  color: #64ffda;
}

@media only screen and (max-width: 1200px) {
  .about {
    padding: 150px 0px;
  }
}
@media only screen and (max-width: 999px) {
  .navbar-style {
    display: none;
  }

  .nav-mobile-larg {
    display: block;
  }

  .nav-mobile {
    display: block;
    z-index: 100;
  }

  .overlay {
    display: none;
  }

  .container {
    padding: 19px 25px;
  }

  .hero .light, .hero .dark {
    font-size: 60px;
  }

  .about {
    padding: 150px 0;
  }

  .about .image-container .img-filter, .about .image-container .back-border {
    width: 280px;
    height: 280px;
  }

  .about .image-container {
    padding-left: 0;
  }

  .about .image-container .back-border {
    top: 30px;
    left: 30px;
  }

  .about .image-container:hover .back-border {
    top: 10px;
    left: 10px;
  }

  .hero {
    padding-bottom: 70px;
  }

  .about, .work-history, .portfolio, .projects, .contact {
    padding: 70px 0;
  }

  .heading:after {
    width: 0px;
  }
}
@media only screen and (max-width: 800px) {
  .portfolio .text {
    grid-column: 1/13 !important;
    grid-row: 1/-1;
    position: relative;
    background: rgba(10, 25, 47, 0.8);
    height: 100%;
    text-align: center !important;
  }
  .portfolio .text p {
    background-color: transparent !important;
  }
  .portfolio .text ul {
    justify-content: center !important;
    padding-left: 0 !important;
  }

  .portfolio .image {
    grid-column: 1/13 !important;
  }
  .portfolio .image .bg-layer {
    background-color: transparent !important;
  }
}